'use strict';

define(['knockout'], function (ko) {

	"use strict";

	ko.bindingHandlers.iframeLoaded = {
		init: function init(element, valueAccessor) {
			// Use this without checking the document has already loaded as we cant tell if its the new document or not
			ko.utils.registerEventHandler(element, 'load', valueAccessor());

			// extending this would be supply the new src in this binding too
		}
	};
});